<template>
  <div class="map-container" :class="allowDrag ? '' : 'allow-drag'">
    <div class="search-box" v-if="hasSearch">
      <div class="search-box_content">
        <span>
          <icon-font name="iconsousuo" size="20"/>
        </span>
        <input v-model="searchStr" class="search-input" type="text" placeholder="搜索地址">
        <span class="search-btn" @click="onReject">取消</span>
      </div>
    </div>
    <div class="map-container">
      <div class="map" ref="map"></div>
      <div class="center-marker" :class="{'center-marker__loading': loading}">
        <img class="center-marker-img" :src="centerMarker" alt="">
        <div v-show="loading" class="loading">
          <img class="loading-icon" :src="loadimg" alt="">
        </div>
        <div class="tip">{{centerTip || centerName}}</div>
      </div>
      <ul class="address-list" v-show="showAddressList">
        <li
          class="address-list-item"
          v-for="(item, index) in searchList"
          :key="index"
          @click="onSearch(item)"
        >
          <p class="address-list-title">{{item.title}}</p>
          <small class="address-list-desc">{{item.address}}</small>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import debounce from '@/utils/debounce';
import getPosition from '@/utils/getPosition'
// 当前位置标记
const ICON = 'data:image/png;base64,';
//中心点标记
const centerMarker = "data:image/png;base64,";
// 中心点 loadimg
const loadimg = 'data:image/png;base64,';
export default {
  name: 'selectPosition',
  props: {
    params: {
      type: Object,
      default () {
        return {}
      }
    },
    // 有搜索栏
    hasSearch: {
      type: Boolean,
      default: true
    },
    // 允许拖拽换点
    allowDrag: {
      type: Boolean,
      default: true
    },
    // 中心点提示
    centerTip: {
      type: String,
      default: '当前选点'
    },
  },
  data () {
    return {
      searchStr: '',
      searchList: [],
      loading: false,
      map: null,
      // 当前位置
      currentPosition: '',
      currentMarker: null,
      // 中心点
      center: '',
      centerAddress: '',
      centerName: '',
      //中心点图标
      centerMarker: centerMarker,
      loadimg: loadimg,
      showAddressList: false,
    }
  },
  created() {

  },
  mounted() {
    //动画延时加载了dom
    this.$nextTick(() => {
      getPosition().then(position => {
        this.currentPosition = `${position.lat},${position.lng}`;
        this.center = (this.params.lat && this.params.lng) ? `${this.params.lat},${this.params.lng}` : `${position.lat},${position.lng}`;
        this.centerAddress = position.address
        this.centerName = position.name
        this.setMap();
        this.qqGeocoder(this.center)
        let [lat, lng] = this.center.split(',');
        this.map && this.map.setCenter(new qq.maps.LatLng(lat, lng));
        //监听拖拽完成时间
        qq.maps.event.addListener(this.map, 'dragend', () => {
          const center = this.map.getCenter()
          this.center = [center.getLat(), center.getLng()].join(',')
          this.qqGeocoder(this.center)
        });
      }).catch(() => {})
    });
  },
  methods: {
    //搜索
    onSearch(v){
      this.center = `${v.location.lat},${v.location.lng}`
      this.centerName = v.address;
      this.showAddressList = false;
      this.searchStr = v.address;
      this.setMapCenter();
    },
    //设置标记点
    setMyMarker () {
      const myPosition = new qq.maps.LatLng(...this.currentPosition.split(','))
      if (!this.currentMarker) {
        const icon = new qq.maps.MarkerImage(ICON, new qq.maps.Size(24, 24), '', new qq.maps.Point(12, 12), new qq.maps.Size(24, 24))
        this.currentMarker = new qq.maps.Marker({
          position: myPosition,
          map: this.map,
          icon
        })
      } else {
        this.currentMarker.setPosition(myPosition)
      }
    },
    //设置地图
    setMap () {
      const center = new qq.maps.LatLng(...this.center.split(','))
      if (this.map) {
        this.map.setCenter(center)
      } else {
        this.map = new qq.maps.Map(this.$refs.map, {
          zoom: 16,
          center,
          zoomControl: false,
          mapTypeControl: false,
          mapZoomType: qq.maps.MapZoomType.CENTER
        })
      }
    },
    //地址解析
    qqGeocoder: debounce( function(center){
      this.loading = true;
      this.$api.qqGeocoder({
        location: center
      }).then(data => {
        this.loading = false;
        this.centerName = data.result.formatted_addresses.recommend;
        this.centerAddress = data.result.formatted_addresses.rough;
        this.setMyMarker()
      }).catch((e) => {})
    },500),
    //设置地图中心点
    setMapCenter (map) {
      const center = new qq.maps.LatLng(...this.center.split(','))
      this.map.setCenter(center)
    },
    // 取消
    onReject () {
      this.$emit('cancel')
    },
  }, 
  beforeRouteEnter (to, from, next) {
    var u = navigator.userAgent;
    //ios终端
    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
    // XXX: 修复iOS版微信HTML5 History兼容性问题
    if (isiOS && to.path !== location.pathname) {
        // 此处不可使用location.replace
        location.assign(to.fullPath)
    } else {
        // console.log(location.assign(to.fullPath))
        next()
    }
  },
  watch: {
    searchStr: debounce( function(vl){
      if (vl !== '' && vl != this.centerName) {
        this.searchList = []
        this.loading = true
        this.$api.qqLocationIp().then(city => {
          this.$api.qqSuggestion({
            keyword: vl,
            page_index: 1,
            page_size: 20,
            policy: 1,
          }, city.result.ad_info.city, '').then(data => {
            this.showAddressList = true;
            this.loading = false
            this.searchList = data.data;
          }).catch(() => {
            this.loading = false
          })
        }).catch(() => {})
      } else {
        this.searchList = []
        this.showAddressList = false;
      }
    }, 500),
    //中心点盖则把数据回调出去
    centerName(centerName){
      let arr = this.center.split(',')
      let location = {
        lat: arr[0],
        lng: arr[1]
      }
      this.$emit('change',{
        location: location,
        address: centerName,
        street: this.centerAddress,
      });
    }
  }
}
</script>

<style lang="scss">
.map-container{
  width: 100%;
  height: 100%;
  .search-box{
    display: flex;
    flex-flow: row;
    width: 100%;
    padding: 0.3333rem;
    .search-box_content {
      width: 100%;
      display: flex;
      align-items: center;
      // background: 
      .search-input{
        flex: 1;
        padding: 3.999px 9.999px;
        -webkit-appearance: none;
        background-color: #FFF;
        background-image: none;
        border: none;
        box-sizing: border-box;
        color: #606266;
        outline: 0;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
      &:focus{
        border-color: #409EFF;
      }
    }
    .search-btn{
        width: 1.6667rem;
        display: inline-block;
        color: #606266;
        
        text-align: center;
        box-sizing: border-box;
        outline: 0;
        margin: 0;
        transition: .1s;
        border-left: 0;
        
        &:active{
          color: #3a8ee6;
          
        }
      }
    }
  }
  .map-container{
    position: relative;
    height: 13.3333rem;
    .map{
      width: 100%;
      height: 100%;
      .smnoprint{
        display: none;
      }
    }
    .center-marker {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      .center-marker-img{
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 1.3333rem;
        margin-left: -0.6667rem;
      }
      .loading {
        position: absolute;
        top: -1.3333rem;
        left: 0rem;
        transition: transform .6s;
        transform: translate3d(-50%, 0, 0);
        .loading-icon {
          width: 0.6667rem;
          filter: invert(100%);
          animation: 1s loading linear infinite;
        }
      }
      .tip {
        position: absolute;
        top: -2.6667rem;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
        height: 1rem;
        padding: 0 0.5rem;
        line-height: 1rem;
        border-radius: 0.5rem;
        font-size: 0.4rem;
        box-shadow: 0 0 0.1rem rgba(0, 0, 0, .1);
        white-space: nowrap;
        background: #fff;
        &:after {
          content: '';
          width: 0;
          height: 0;
          display: block;
          border-left: 0.1667rem solid transparent;
          border-right: 0.1667rem solid transparent;
          border-top: 0.1667rem solid #fff;
          position: absolute;
          top: 1rem;
          left: calc(50% - 0.1667rem);
        }
      }
    }
    .address-list{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      max-height: 80%;
      background-color: #fff;
      box-shadow: 0 0 0.1rem rgba(0, 0, 0, .1);
      overflow-y: scroll;
      .address-list-item{
        padding: 0.3333rem;
        border-bottom: 0.0333rem solid #ebebeb;
        text-align: left;
        &:hover{
          color: #409EFF;
          border-color: #c6e2ff;
          background-color: #ecf5ff;
        }
        &:active{
          color: #3a8ee6;
          background-color: #c6e2ff;
        }
        .address-list-desc{
          color: #999;
        }
      }
    }
  }
  &.allow-drag{
    pointer-events: none;
  }
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
</style>



