<template><section>
<h1>date-time-picker时间选择器</h1>
<hr>
<p>用于选择时间，支持日期、时分等时间维度，通常与 弹出层 组件配合使用</p>
<h3>基础用法</h3>
<demo-block> <div slot="desc">
<date-time-picker></date-time-picker>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">date-time-picker</span>&gt;</span>


</code></pre>
</div></demo-block>
<h3>选择年月日</h3>
<p>使用currentDate属性可以自定义时间点， 默认值是当期时间点</p>
<demo-block> <div slot="desc">
<date-time-picker type="year-month-day" current-date="2019-10-08">
</date-time-picker></div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span> 
  <span class="hljs-attr">type</span>=<span class="hljs-string">"year-month-day"</span> 
  <span class="hljs-attr">current-date</span>=<span class="hljs-string">"2019-10-08"</span> 
  /&gt;</span>
</code></pre>
</div></demo-block>
<h3>选择年月</h3>
<p>如果设置的当期时间点小于设置的最小时间， 那么会默认选择该列的第一项， 同理， 如果大于了最大时间， 就会默认选择最后一项</p>
<demo-block> <div slot="desc">
<date-time-picker type="year-month" :min-date="new Date()" current-date="2018-10">

</date-time-picker></div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"year-month"</span> <span class="hljs-attr">:min-date</span>=<span class="hljs-string">"new Date()"</span> <span class="hljs-attr">current-date</span>=<span class="hljs-string">"2018-10"</span>/&gt;</span>

</code></pre>
</div></demo-block>
<h3>选择时间</h3>
<demo-block> <div slot="desc">
<date-time-picker type="hours-minutes" currentDate="18:20" :min-hours="17">


</date-time-picker></div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"hours-minutes"</span> <span class="hljs-attr">currentDate</span>=<span class="hljs-string">"18:20"</span> <span class="hljs-attr">:min-hours</span>=<span class="hljs-string">"17"</span>/&gt;</span>


</code></pre>
</div></demo-block>
<h3>格式化某列</h3>
<p>有时候我们需要对选项进行一些格式化和过滤，下面例子对天数这一列实现了自定义时间间隔，对年份和月份文字进行了格式化处理，参数<code v-pre class="code_inline">type</code>的类型有<code v-pre class="code_inline">year</code>, <code v-pre class="code_inline">month</code>, <code v-pre class="code_inline">days</code>, <code v-pre class="code_inline">hours</code>, <code v-pre class="code_inline">minutes</code></p>
<demo-block> <div slot="desc">
<date-time-picker type="year-month-day" :filter="filter" :formatter="format">



</date-time-picker></div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span> 
  <span class="hljs-attr">type</span>=<span class="hljs-string">"year-month-day"</span> 
  <span class="hljs-attr">:filter</span>=<span class="hljs-string">"filter"</span> 
  <span class="hljs-attr">:formatter</span>=<span class="hljs-string">"format"</span>/&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
 <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      filter (type, options) {
        <span class="hljs-keyword">if</span> (type == <span class="hljs-string">'days'</span>) {
          <span class="hljs-keyword">return</span> options.filter(<span class="hljs-function"><span class="hljs-params">v</span> =&gt;</span> v % <span class="hljs-number">2</span> == <span class="hljs-number">0</span>)
        }
        <span class="hljs-keyword">return</span> options
      },
      format (type, value) {
        <span class="hljs-keyword">if</span> (type == <span class="hljs-string">'year'</span>) {
          <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${value}</span>年`</span>
        } 
        <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (type == <span class="hljs-string">'month'</span>) {
          <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${value}</span>月`</span>
        }
        <span class="hljs-keyword">return</span> value
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>不常用属性</h3>
<demo-block> <div slot="desc">
<date-time-picker type="year-month-day" :item-height="60" :select-item-height="80" :visible-item-count="7" confirm-button-text="确认按钮" cancel-button-text="取消按钮">


</date-time-picker></div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">date-time-picker</span> 
  <span class="hljs-attr">type</span>=<span class="hljs-string">"year-month-day"</span> 
  <span class="hljs-attr">:item-height</span>=<span class="hljs-string">"60"</span> 
  <span class="hljs-attr">:select-item-height</span>=<span class="hljs-string">"80"</span> 
  <span class="hljs-attr">:visible-item-count</span>=<span class="hljs-string">"7"</span>
  <span class="hljs-attr">confirm-button-text</span>=<span class="hljs-string">"确认按钮"</span>
  <span class="hljs-attr">cancel-button-text</span>=<span class="hljs-string">"取消按钮"</span>/&gt;</span>


</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>datetime, year-month-day, year-month, hours-minutes</td>
<td>datetime</td>
</tr>
<tr>
<td>current-date</td>
<td>自定义时间点</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>min-date</td>
<td>可选的最小时间，精确到分钟</td>
<td>Date</td>
<td>-</td>
<td>十年前</td>
</tr>
<tr>
<td>max-date</td>
<td>可选的最大时间，精确到分钟</td>
<td>Date</td>
<td>-</td>
<td>十年后</td>
</tr>
<tr>
<td>max-hours</td>
<td>可选的最大小时，针对 hours-minutes 类型</td>
<td>number</td>
<td>0 - 23</td>
<td>0</td>
</tr>
<tr>
<td>min-hours</td>
<td>可选的最小小时，针对 hours-minutes 类型</td>
<td>number</td>
<td>0 - 23</td>
<td>23</td>
</tr>
<tr>
<td>max-minutes</td>
<td>可选的最大分钟，针对 hours-minutes 类型</td>
<td>number</td>
<td>0-59</td>
<td>59</td>
</tr>
<tr>
<td>min-minutes</td>
<td>可选的最小分钟，针对 hours-minutes 类型</td>
<td>number</td>
<td>0-59</td>
<td>十年前</td>
</tr>
<tr>
<td>item-height</td>
<td>选项高度</td>
<td>number</td>
<td>-</td>
<td>44</td>
</tr>
<tr>
<td>select-item-height</td>
<td>中间选中框高度</td>
<td>number</td>
<td>-</td>
<td>44</td>
</tr>
<tr>
<td>visible-item-count</td>
<td>可见的选项个数</td>
<td>number</td>
<td>-</td>
<td>5</td>
</tr>
<tr>
<td>is-tab-btn</td>
<td>是否显示顶部栏</td>
<td>boolean</td>
<td>true, false</td>
<td>true</td>
</tr>
<tr>
<td>filter</td>
<td>选项过滤函数</td>
<td>(type, values) =&gt; values</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>formatter</td>
<td>选项格式化函数</td>
<td>(type, value) =&gt; value</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>confirm-button-text</td>
<td>确认按钮文字</td>
<td>string</td>
<td>-</td>
<td>确认</td>
</tr>
<tr>
<td>cancel-button-text</td>
<td>取消按钮文字</td>
<td>string</td>
<td>-</td>
<td>取消</td>
</tr>
<tr>
<td>is-addZero</td>
<td>返回的结果， 不足两位数是否自动补0</td>
<td>boolean</td>
<td>true, false</td>
<td>true</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当值变化时触发的事件</td>
<td>当前 value</td>
</tr>
<tr>
<td>confirm</td>
<td>点击完成按钮时触发的事件</td>
<td>当前 value</td>
</tr>
<tr>
<td>cancel</td>
<td>点击取消按钮时触发的事件</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
 export default {
   data(){
     return {
       position: {}
     }
   },
    methods: {
      onCancel() {
        alert('onCancel');
      },
      showSelectPosition(){
        this.$popup(mapSelectPosition, {
          hasSearch: true,
          allowDrag: true,
          centerTip: '吃饭没',
          params: {
            lat: 28.228259,
            lng: 112.938904
          }
        }).then(position => {
          this.position = position;
        });
      },

      filter (type, options) {
        if (type == 'days') {
          return options.filter(v => v % 2 == 0)
        }
        return options
      },

      format (type, value) {
        if (type == 'year') {
          return `${value}年`
        } 
        else if (type == 'month') {
          return `${value}月`
        }
        return value
      }
    },
  }
</script>


