<template><section>

<h1>$popup 弹窗</h1>
<hr>
<p>弹出模态框，常用于消息提示、消息确认、在当前页面内完成特定的交互操作</p>
<p>弹出框组件只支持函数调用 返回一个Promise</p>
<h3>地图选点</h3>
<demo-block> <div slot="desc">
<div>
  <van-button type="info" @click="showSelectPosition" :round="false">显示弹窗</van-button>
  <p>选中的地址{{JSON.stringify(position)}}</p>
</div>


</div><div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">van-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showSelectPosition"</span> <span class="hljs-attr">:round</span>=<span class="hljs-string">"false"</span>&gt;</span>显示弹窗<span class="hljs-tag">&lt;/<span class="hljs-name">van-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>选中的地址{{JSON.stringify(position)}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> mapSelectPosition <span class="hljs-keyword">from</span> <span class="hljs-string">'../../packages/popup/src/mapSelectPosition'</span>
 <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
     <span class="hljs-keyword">return</span> {
       <span class="hljs-attr">position</span>: {}
     }
   },
    <span class="hljs-attr">methods</span>: {
      onCancel() {
        alert(<span class="hljs-string">'onCancel'</span>);
      },
      showSelectPosition(){
        <span class="hljs-keyword">this</span>.$popup(mapSelectPosition, {
          <span class="hljs-attr">hasSearch</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">allowDrag</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">centerTip</span>: <span class="hljs-string">'吃饭没'</span>,
          <span class="hljs-attr">params</span>: {
            <span class="hljs-attr">lat</span>: <span class="hljs-number">28.228259</span>,
            <span class="hljs-attr">lng</span>: <span class="hljs-number">112.938904</span>
          }
        }).then(<span class="hljs-function"><span class="hljs-params">position</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.position = position;
        });
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>hasSearch</td>
<td>有搜索栏</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>allowDrag</td>
<td>允许拖拽换点</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>centerTip</td>
<td>中心点提示</td>
<td>string</td>
<td>—</td>
<td>当前选点</td>
</tr>
<tr>
<td>params</td>
<td>扩展参数（目前只有经纬度）</td>
<td>object</td>
<td>lat纬度，lng经度</td>
<td>当前选点</td>
</tr>
</tbody>
</table>
