</section></template>
<style>
.x-alert:not(:first-child){
  margin-top: 10px;
}
</style>
<script>
import mapSelectPosition from '../../packages/popup/src/mapSelectPosition'
 export default {
   data(){
     return {
       position: {}
     }
   },
    methods: {
      onCancel() {
        alert('onCancel');
      },
      showSelectPosition(){
        this.$popup(mapSelectPosition, {
          hasSearch: true,
          allowDrag: true,
          centerTip: '吃饭没',
          params: {
            lat: 28.228259,
            lng: 112.938904
          }
        }).then(position => {
          this.position = position;
        });
      }
    }
  }
</script>


